namespace C2D {
    export class E extends Error {
        public static ELEMENT_DEFERRED: string = '';

        /**
         * 
         */
        constructor(message: string) {
            if ('captureStackTrace' in Error)
                Error['captureStackTrace'](this, E);
            super();
            this.name = 'BigineC2DError';
            this.message = message;
        }
    }
}
